/* ===========================
          LOADER
=========================== */
.loader {
  position: fixed;
  inset: 0;
  background: radial-gradient(circle at center, #01030a 0%, #000 85%);
  z-index: 9999;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 1s ease;
}

.loader.fade-out {
  opacity: 0;
  pointer-events: none;
}

/* ===========================
        FLASH APOCALISSE
=========================== */
.apocalypse::after {
  content: "";
  position: absolute;
  inset: 0;
  background: #bfe0ff;
  opacity: 0;
  animation: apocalypseFlash 1.6s infinite;
  pointer-events: none;
  mix-blend-mode: screen;
}

@keyframes apocalypseFlash {
  0%, 82%, 100% { opacity: 0; }
  83% { opacity: 0.25; }
  85% { opacity: 0; }
  88% { opacity: 0.6; }
  90% { opacity: 0; }
}

/* ===========================
        DISTORSIONE OTTICA
=========================== */
.ultimate-distortion {
  animation: distortion 0.25s infinite;
}

@keyframes distortion {
  0% { filter: none; }
  50% { filter: contrast(120%) brightness(130%) saturate(120%); }
  100% { filter: none; }
}

/* ===========================
        BAGLIORE BORDI
=========================== */
.loader::before {
  content: "";
  position: absolute;
  inset: 0;
  box-shadow:
    inset 0 0 60px #ff0000,
    inset 0 0 100px rgba(255, 0, 0, 0.6);
  opacity: 0;
  animation: borderGlow 2s infinite;
  pointer-events: none;
}

@keyframes borderGlow {
  0%, 86%, 100% { opacity: 0; }
  87% { opacity: 1; }
  89% { opacity: 0; }
}

/* ===========================
        LOGO CENTRALE
=========================== */
.lightning-origin {
  position: relative;
  z-index: 50;
}

.logo {
  width: clamp(150px, 15vw, 330px);
  filter:
    drop-shadow(0 0 10px #ff0000)
    drop-shadow(0 0 40px #ff2f2f);
}

/* Tremolio distruttivo */
.mega-shake {
  animation: megaShakeSoft 0.25s infinite;
}

@keyframes megaShakeSoft {
  0%   { transform: translate(0); }
  20%  { transform: translate(-1px, 1px); }
  40%  { transform: translate(1px, -1px); }
  60%  { transform: translate(-1.5px, -0.5px); }
  80%  { transform: translate(1px, 1px); }
  100% { transform: translate(0); }
}


/* ===========================
        FULMINI REALI
=========================== */
.lightning {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 14px;
  height: 200vh;               /* ✅ ULTRA LUNGHI RESPONSIVE */
  transform-origin: top;
  opacity: 0;
  stroke: #ff0000;
  stroke-width: 4;
  fill: none;
  filter:
    drop-shadow(0 0 15px #ff0000)
    drop-shadow(0 0 40px #ff0000)
    drop-shadow(0 0 120px #8fb8ff);
}

/* ===========================
        RAMIFICAZIONI
=========================== */
.lightning::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    180deg,
    transparent,
    #4d8dff,
    transparent
  );
  width: 3px;
  height: 60%;
  left: 30%;
  top: 20%;
  transform: rotate(18deg);
  opacity: 0.6;
}

/* ===========================
        ANIMAZIONI CAOTICHE
=========================== */
.bolt.a { animation: strikeA 0.9s infinite steps(1); }
.bolt.b { animation: strikeB 1.4s infinite steps(1); }
.bolt.c { animation: strikeC 2.1s infinite steps(1); }

@keyframes strikeA {
  0%, 68%, 100% { opacity: 0; }
  69% {
    opacity: 1;
    transform: translate(-50%, -50%) rotate(22deg);
  }
  71% { opacity: 0; }
}

@keyframes strikeB {
  0%, 75%, 100% { opacity: 0; }
  76% {
    opacity: 1;
    transform: translate(-50%, -50%) rotate(-38deg);
  }
  79% { opacity: 0; }
}

@keyframes strikeC {
  0%, 83%, 100% { opacity: 0; }
  84% {
    opacity: 1;
    transform: translate(-50%, -50%) rotate(55deg);
  }
  87% { opacity: 0; }
}

/* ======================================
   MIGLIORIE APOCALISSE PER SMARTPHONE
====================================== */

@media (max-width: 768px) {

  /* Logo leggermente più grande rispetto allo schermo */
  .logo {
    width: clamp(90px, 22vw, 140px);
  }

  /* Riduciamo la distorsione globale (su mobile stanca) */
  .ultimate-distortion {
    animation: distortion 0.45s infinite;
  }

  /* Fulmini più SPESSI e più LUMINOSI */
  .lightning {
    width: 22px;              /* prima erano ~14px */
    height: 240vh;           /* ancora più lunghi per coprire tutto */
    stroke-width: 6;

    filter:
      drop-shadow(0 0 25px #ff0000)
      drop-shadow(0 0 40px #ff0000)
      drop-shadow(0 0 140px #9fc2ff);
  }

  /* Ramificazioni più evidenti */
  .lightning::after {
    width: 4px;
    opacity: 0.9;
  }

  /* Fulmini più FREQUENTI su mobile */
  .bolt.a { animation-duration: 0.8s; }
  .bolt.b { animation-duration: 1.1s; }
  .bolt.c { animation-duration: 1.6s; }

  /* Vibrazione logo ancora più morbida su mobile */
  .mega-shake {
    animation: megaShakeSoft 0.35s infinite;
  }

  /* Flash più visibile su schermi piccoli */
  .apocalypse::after {
    animation-duration: 1.2s;
  }

  /* Bagliore bordi più forte su mobile */
  .loader::before {
    box-shadow:
      inset 0 0 50px #ff0000,
      inset 0 0 100px rgba(255, 55, 55, 0.8);
  }
}

/* ======================================
   EXTRA PER SCHERMI ULTRA PICCOLI (≤ 400px)
====================================== */
@media (max-width: 400px) {

  .lightning {
    width: 26px;
    stroke-width: 7;
  }

  .logo {
    width: 110px;
  }
}
